.tb_g_p_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.tb_g_p_post_wrapper{
    width: 25%;
    padding: 7.5px;
}

.tb_g_p_post_in{
    display: block;
    position: relative;
    cursor: pointer;
    overflow: hidden;
    background-color: var(--tb-bg-color);
    border-radius: var(--tb-border-radius);
}

.tb_g_p_post_media_wrapp{
    position: relative;
}

.tb_g_p_post_details, .tb_g_p_post_overlay {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 10px;
    opacity: 0;
    visibility: hidden;
    transition: opacity .3s ease, visibility .3s ease;
}

.tb_g_p_post_overlay {
    z-index: 2;
}

.tb_g_p_post_overlay-light{
    background-color: rgba(255, 255, 255, .3);
    background-image: linear-gradient(135deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, .4) 100%);
}

.tb_g_p_post_overlay-dark{
    background-color: rgba(0, 0, 0, .2);
    background-image: linear-gradient(135deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, .4) 100%);
}

.tb_g_p_post_details {
    z-index: 4;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

@media only screen and (min-width: 991px)  {
    .tb_g_p_post_in:hover .tb_g_p_post_details,
    .tb_g_p_post_in:hover .tb_g_p_post_overlay {
        opacity: 1;
        visibility: visible;
    }
    .tb_g_p_post_in:hover .tb_g_p_post_overlay.tb_g_p_color_overlay {
        opacity: .8;
    }

    .tb_g_p_post_ani:hover .tb_g_p_post_details{
        transform: translateY(10px);
        animation: TbSlideUpAnimation_ .65s ease forwards;
    }
    @-webkit-keyframes TbSlideUpAnimation_ {
        100% {
            transform: translateY(0);
        }
        100% {
            transform: translateY(0);
        }
    }
    
    @keyframes TbSlideUpAnimation_ {
        0% {
            transform: translateY(10px);
        }
        100% {
            transform: translateY(0);
        }
    }
}

.tb_g_p_post_in:hover .tb__media_ico_ .tb__icon,
.tb_g_p_post_in:hover .tb__media_ico_ .tb__icon{
    opacity: 0;
    transform: scaleX(.4) scaleY(.4) translateZ(0);
}

.tb_g_p_contant_wrapper {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    margin: 0 auto;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 15px;
}


.tb_g_p_image_wrap_, .tb_g_p_video_wrap_{
    position: relative;
    padding-bottom: 100%;
    background-color: rgba(0,0,0,.03);
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_wall_container .tb_g_p_image_wrap_, 
.tb_wall_container .tb_g_p_video_wrap_{
    padding-bottom: calc((var(--tb-theme-height) - (var(--tb-padding) * var(--tb-grid-row))) / var(--tb-grid-row));
}

.tb_g_p_image_wrap_in {
    position: absolute;
    height: 100%;
    margin: auto;
    left: 0;
    right: 0;
    bottom: 0;
    top: 0;
    width: 100%;
}


.tb_g_p_image {
    width: 100% !important;
    height: 100% !important;
    display: block;
    object-fit: cover;
    object-position: center;
}
.tb_g_p_image_wrap_ img{
    max-width: 100%;
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    margin: 0 !important;
}

.tb_g_p_image, .tb_g_p_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
    background-color: #000;
}

.tb_g_p_video video{
    height: 100%;
    width: 100%;
    object-fit: cover;
    object-position: center;
}

.tb_g_p_video iframe{
    pointer-events: none;
}

.tb_g_p_media_iframe_video span,
.tb_g_p_media_iframe_video iframe {
    height: 100% !important;
    display: block !important;
    width: 100% !important;
    border: none;
}

.tb_g_p_media_iframe_video video {
    border: none;
    width: 100%;
    height: 100%;
    object-position: center;
    object-fit: cover;
}

.tb_g_p_social_ {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 15px 0;
    flex-shrink: 0;
}

.tb_g_p_social_ico_ {
    font-size: 22px;
}

.tb_g_p_author {
    width: calc(100% - 30px);
}

.tb_g_p_author_profile {
    height: 44px;
    width: 44px;
    border-radius: 50%;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    background-color: rgba(0,0,0,.2);
    overflow: hidden;
}
.tb_g_p_author_profile__{
    height: 100% !important;
    width: 100% !important;
    margin: 0;
    object-fit: cover;
    object-position: center;
}

.tb_g_p_authorname {
    font-size: 14px;
    font-weight:  var(--tb-author-font-weight);
    margin-bottom: 4px;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-align: left;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_g_p_authorname,
.tb_g_p_username,
.tb_g_p_time {
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
    font-style:  var(--tb-author-font-style);
}

.tb_g_p_seprator {
    background-color: var(--tb-author-color);
}

.tb_g_p_content {
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
}

.tb_g_p_author {
    display: flex;
    align-items: center;
    flex-grow: 1;
}

.tb_g_p_author_wrapper {
    display: flex;
    align-items: center;
    position: absolute;
    width: 100%;
    bottom: 0;
    left: 0;
    padding: 10px;
}

.tb_g_p_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: calc(100% - 54px);
    padding-right: 10px;
}

.tb_g_p_post_info {
    display: flex;
    align-items: center;
}

.tb_g_p_seprator {
    height: 4px;
    width: 4px;
    min-width: 4px;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_g_p_username, .tb_g_p_time {
    font-size: 10px;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_g_p_username{
    max-width: 65%;
}

.tb_g_p_time{
    max-width: 35%;
}

.tb_g_p_social__ico {
    font-size: 24px;
    color: var(--tb-icon-color);
}

.tb_g_p_social_top_{
    position: absolute;
    z-index: 4;
    right: 10px;
    top: 10px;
}

.tb_g_p_content {
    word-break: break-word;
    white-space: pre-line;
    text-align: center;
    white-space: pre-line;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 4;
    text-overflow: ellipsis;
    width: 100%;
}

.tb_g_p_content .tb_text_links_{
    color: inherit !important;
    text-decoration: underline !important;
    display: inline;
}

.tb_g_p_post_in:hover .tb_share_wrapper{
    opacity: 1;
    visibility: visible;
}

.tb_g_p_video_ico, .tb_g_p_audio_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    z-index: 2;
    color: #fff;
    font-size: 12px;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
}
.tb_g_p_video_ico{
    font-size: 12px;
}
.tb_g_p_audio_ico{
    font-size: 16px;
}
.tb_g_p_multi_images_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    color: #fff;
    font-size: 17px;
    z-index: 2;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
}

.tb_g_p_multi_images_ico_bottom {
    position: absolute;
    right: 12px;
    bottom: 12px;
    color: #fff;
    font-size: 17px;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
}

.tb_g_p_youtube_ico {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 34px;
}

.tb_g_p_media_icon_te.tb__media_ico_ .tb__icon:nth-child(1) {
    right: auto;
    left: var(--tb-icon-position);
    top: var(--tb-icon-position);
}

.tb_g_p_media_icon_te.tb__media_ico_ .tb__icon:nth-child(2) {
    right: auto;
    left: var(--tb-icon-position);
    top: auto;
    bottom: var(--tb-icon-position);
}

.tb_g_p_media_icon_te.tb__media_ico_ .tb__icon:nth-child(3) {
    left: auto;
    right: var(--tb-icon-position);
    top: auto;
    bottom: var(--tb-icon-position);
}

.tb_g_p_post_wrapper2x .tb_g_p_author_profile {
    height: 30px;
    width: 30px;
    margin-right: 5px;
}

.tb_g_p_post_wrapper2x .tb_g_p_authorname {
    font-size: 10px;
    margin-bottom: 0px;
}

.tb_g_p_post_wrapper2x .tb_g_p_post_info {
    display: none;
}

.tb_g_p_post_wrapper2x .tb_g_p_contant_wrapper {
    padding: 7px;
}

.tb_g_p_post_wrapper2x .tb_g_p_content{
    -webkit-line-clamp: 2;
}

.tb_g_p_post_wrapper2x .tb_g_p_author_info {
    padding-right: 10px;
    width: calc(100% - 30px);
}

.tb_g_p_post_wrapper2x .tb_g_p_author_wrapper {
    padding: 5px;
}

.tb_g_p_media_icon .tb_multi_images_ico{
    right: auto;
    left: 12px;
    z-index: 3;
}


.tb_g_p_post_in:hover .tb_g_p_like.tb_like_ico{
    bottom: 70px;
}

.tb_g_p_like.tb_like_ico{
    bottom: 6px;
    right: 6px;
    background-color: transparent;
    box-shadow: none;
    transition: all .3s ease-out;
}

.tb_g_p_like.tb_like_ico .tb__icon{
    color: #fff;
    font-size: 25px;
}

.tb_g_p_like.tb_animate .tb__icon{
    color: red;
}